<template>
  <div class="search_result">
    <h1>Showing {{ filtered.length }} results for: {{ this.$route.params.s }}</h1>
    <Card class="result" v-for="(info, id) in filtered" :key="id" :cardref="info.href">
      <!-- {{info}} -->
      <div slot="title">{{ info.name }}</div>
      <!-- {{info.p}} -->
      <div slot="descr">
        {{ info.descr }} <br />
      </div>
    </Card>
  </div>
</template>

<script>
import views from "@/views/view_docs/views.json";
import Card from "@/components/search/Card.vue";

export default {
  name: "search",
  components: {
    Card
  },
  data() {
    return {
      query: null,
      results: null,
    };
  },
  computed: {
    filtered() {
      let search = this.$route.params.s
      return Object.values(views.views).filter(query => {
        if (query.tags) {
          for (let tag of query.tags) {
            if (tag.toLowerCase().includes(search.toLowerCase())) {
              return true;
            }
          }
          if (query.raw_text){
            return query.raw_text.includes(search.toLowerCase());
          }
          return false;
        }
        return false;
      });
    }
  },
  created() {
    this.query = this.$route.params.s;
  }
};
</script>

<style lang="scss">
.search_result {
  // height: 100rem;
  padding: 2rem;
  padding-left: 3rem;
}
</style>
